.apology-section {
	position: relative;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
	}
	.appology-wrap {
		position: relative;
		padding: 3rem 1.5rem;
		border-radius: 10px;
		overflow: hidden;
		background: var(--color-primary);
		@include max-md {
			padding: 2.25rem 1rem;
		}
		.orn-1,
		.orn-2 {
			position: absolute;
			left: 0;
			top: 0;
			height: 100%;
		}
		.orn-1 {
			transform: translate(-40%, 60%);
			height: 320px;
			@include max-md {
				height: 200px;
				transform: translate(-40%, 120%);
			}
		}
		.orn-2 {
			right: 0;
			left: unset;
			height: 240px;
			transform: translate(35%, -30%) scaleX(-1);
			@include max-md {
				height: 140px;
			}
		}
	}
}
