.rsvp-section {
	position: relative;
	padding: 3rem 0;
	.image-wrap {
		position: relative;
		height: 520px;
		border-radius: 10px;
		position: absolute;
		top: -8rem;
		left: 0;
		@include max-md {
			position: relative;
			top: 0;
			height: 380px;
		}
		.rsvp-background {
			width: 100%;
			height: 100%;
			object-fit: cover;
			border-radius: 10px;
		}
	}
	.rsvp-wrap {
		padding: 3rem 2rem;
		border-radius: 10px;
		background-color: var(--color-primary);
		overflow: hidden;
		position: relative;
		@include max-md {
			padding: 2.5rem 1rem;
		}
		.orn-1,
		.orn-2 {
			position: absolute;
			left: 0;
			top: 0;
			height: 100%;
			@include max-md {
				height: 70%;
			}
		}
		.orn-1 {
			transform: translate(-40%, 50%);
			@include max-md {
				transform: translate(-40%, 90%);
			}
		}
	}
}
