.gallery-section {
	position: relative;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
	}
	.img-gallery {
		height: 280px;
		width: 100%;
		object-fit: cover;
		margin-bottom: 1rem;
		@include max-lg {
			height: 246px;
		}
		@include max-md {
			height: 276px;
		}
	}
	.gallery {
		position: relative;
		width: 100%;
		@include max-md {
			width: 90%;
			margin: auto;
		}
		.overlay-left,
		.overlay-right {
			width: 150px;
			height: 100%;
			position: absolute;
			top: -1px;
			z-index: 5;
			@include max-sm {
				width: 100px;
			}
		}
	}
	.splide {
		margin-bottom: 0;
		.splide__track {
			overflow: visible;
			position: relative;
		}
		.splide__arrow {
			border-radius: 0;
			background-color: var(--color-secondary);
			width: 45px;
			height: 45px;
			z-index: 10;
			border-radius: 1000px;
			transform: translateY(-80%);
			@include max-lg {
				width: 30px;
				height: 30px;
			}
			svg {
				fill: #fff;
				width: 25px;
				height: 30px;
				@include max-md {
					width: 15px;
					height: 20px;
				}
			}
			&--prev {
				left: -22.5px;
				@include max-lg {
					left: -15px;
				}
			}
			&--next {
				right: -22.5px;
				@include max-lg {
					right: -15px;
				}
			}
		}
	}
}
