.stream-section {
	position: relative;
	padding: 3rem 0;
	text-align: center;
	@include max-md {
		.row {
			gap: 1.5rem;
		}
	}
	@include min-md {
		.col-md-6 {
			padding: 0;
		}
	}
	.rose-bg {
		position: absolute;
		bottom: 0;
		left: 50%;
		transform: translate(-50%, 10%);
		height: 110%;
	}
	.stream-wrapper {
		position: relative;
		background-color: var(--color-primary);
		border-radius: 10px;
		text-align: center;
		padding: 3rem 2rem;
		@include max-md {
			padding: 2.25rem 1rem;
		}
		h4 {
			text-align: center;
		}
	}
	ul {
		ol {
			padding: 0;
		}
	}
	.card {
		background-color: transparent;
		height: 100%;
		color: var(--color-dark);
		border: none;
	}
	.btn-custom {
		width: 50%;
		@include max-lg {
			width: 100%;
		}
		@include max-md {
			width: fit-content;
		}
	}
	.yt-embed {
		@include min-lg {
			height: 320px;
		}
	}
	.yt-description {
		width: 100%;
		@include max-md {
			text-align: center;
		}
	}
}
