.story-section {
	position: relative;
	padding: 0 0 5rem;
	@include max-md {
		padding: 0 0 3rem;
	}
	.story-description {
		position: relative;
		text-align: center;
		padding: 3rem 2rem;
		background-color: var(--color-primary);
		margin-top: -3rem;
		z-index: 10;
		border-radius: 10px;
		overflow: hidden;
		@include max-lg {
			margin-top: -1.5rem;
		}
		@include max-md {
			padding: 2rem 0.75rem;
			margin-top: 2rem;
		}
		.orn-1,
		.orn-2 {
			position: absolute;
			left: 0;
			top: 0;
			height: 200px;
		}
		.orn-1 {
			right: 0;
			left: unset;
			transform: translate(60%, 100%) scaleX(-1);
			@include max-lg {
				height: 180px;
				transform: translate(65%, 150%) scaleX(-1);
			}
			@include max-md {
				transform: translate(60%, 180%) scaleX(-1);
			}
		}
	}
	.image-story-wrap {
		position: relative;
		height: 520px;
		width: 100%;
		@include max-md {
			height: 480px;
		}
		&::before {
			position: absolute;
			top: 0;
			right: 0;
			bottom: 0;
			left: 0;
			background-color: rgba($color: #000000, $alpha: 0.3);
			content: "";
			z-index: 2;
		}
		.story-image {
			object-fit: cover;
			width: 100%;
			height: 100%;
		}
	}
}
