.venue-section {
	position: relative;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
	}
	.rose-bg {
		position: absolute;
		bottom: 0;
		left: 50%;
		transform: translate(-50%, 0);
		height: 110%;
	}
	.btn-close {
		position: absolute;
		right: 0;
		top: -25px;
		@include max-md {
			right: 0;
		}
	}
	.venue-wrapper {
		position: relative;
		.venue-content {
			position: relative;
			text-align: center;
			#maps {
				transform: translateX(100%);
				display: none;
				position: absolute;
			}
			.event {
				top: 0;
				position: relative;
			}
			.card {
				border: none;
				background-color: var(--color-primary);
				height: 100%;
				border-radius: 10px;
				position: relative;
				overflow: hidden;
				.orn-1,
				.orn-2 {
					position: absolute;
					top: 0;
					height: 220px;
					@include max-lg {
						height: 180px;
					}
					@include max-md {
						height: 130px;
					}
				}
				.orn-1 {
					right: 0;
					transform: translate(50%, -20%) scaleX(-1);
				}
				.orn-2 {
					right: 0;
					left: unset;
					transform: translate(35%, 125%) scaleX(-1);
					@include max-lg {
						transform: translate(50%, 180%) scaleX(-1);
					}
					@include max-md {
						transform: translate(50%, 220%) scaleX(-1);
					}
				}
				.blossom-wrapper {
					.animation-loop {
						.blossom {
							@for $i from 1 through 6 {
								&.pos-#{$i} {
									animation-duration: random($limit: 8s) + 2s;
									animation-delay: random($limit: 20s) + 0s;
								}
							}
							@for $i from 7 through 12 {
								&.pos-#{$i} {
									animation-duration: random($limit: 8s) + 2s;
									animation-delay: random($limit: 15s) + 0s;
								}
							}
						}
					}
				}
				.card-body {
					position: relative;
				}
			}
		}
	}
	.prokes-wrap {
		display: flex;
		gap: 0.75rem;
		justify-content: center;
		align-items: flex-start;
		flex-wrap: wrap;
		text-align: center;
		.prokes {
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: flex-start;
			width: 132px;
			@include max-md {
				width: 120px;
			}
		}
	}
	.row-venue {
		@include max-md {
			gap: 3rem;
		}
	}
	.event-name {
		position: relative;
		h5 {
			font-weight: 600;
		}
		p {
			margin-bottom: 0;
		}
	}
	.btn-custom {
		background-color: transparent;
		border-color: var(--color-dark);
		color: var(--color-dark);
		transition: all 0.25 ease-in-out;
		&:hover {
			background-color: var(--color-secondary);
			border-color: var(--color-secondary);
			color: #fff;
		}
	}
}

.maps-embed {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
}
