.countdown {
	display: flex;
	justify-content: center;
	align-items: center;
	text-align: center;
	position: relative;
	margin: 0;
	gap: 1.75rem;
	padding: 1rem 0;
	border-radius: 5px;
	z-index: 3;
	@include max-md {
		gap: 0.25rem;
	}
	overflow: hidden;
	.orn-1,
	.orn-2 {
		position: absolute;
		left: 0;
		top: 0;
		height: 100%;
	}
	.orn-1 {
		transform: translate(-40%, 40%);
	}
	.orn-2 {
		right: 0;
		left: unset;
		transform: translate(35%, -30%) scaleX(-1);
	}
	.days,
	.hours,
	.minutes,
	.seconds {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		position: relative;
		background-color: transparent;
		border-radius: 1000px;
		width: 78px;
		@include max-md {
			width: 65px;
		}
		.huruf {
			font-size: 14px;
			font-weight: 600;
		}
		.angka {
			margin-bottom: 0;
			font-weight: 500;
			font-size: 38px;
			font-family: $font-secondary;
			color: var(--color-secondary);
			@include max-sm {
				font-size: 26px;
			}
		}
		.huruf,
		.angka {
			margin: 0;
			padding: 0;
			line-height: normal;
		}
	}
}
