.logo-viding {
	height: 2.4rem;
	path {
		fill: #fff;
	}
	@include max-md {
		width: 2.25rem;
	}
}

.header-section {
	position: relative;
	height: 100vh;
	width: 100%;
	&::before {
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		background: rgba($color: #000000, $alpha: 0.5);
		content: "";
		z-index: 2;
	}
	.image-wrapper {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		.background-header {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}
	.header-content {
		position: relative;
		text-align: center;
		padding: 4rem 0 0;
		.header {
			text-align: center;
			width: 100%;
			height: 100%;
			display: flex;
			flex-direction: column;
			justify-content: flex-start;
			align-items: center;
			padding: 1.5rem 0 3rem;
			position: relative;
			z-index: 3;
			.header-title {
				color: #fff;
				position: relative;
				.title {
					color: #fff;
				}
			}
		}
		.reminder-wrap {
			position: absolute;
			bottom: 0;
			left: 50%;
			transform: translate(-50%, 65%);
			display: flex;
			flex-direction: column;
			justify-content: center;
			align-items: center;
			gap: 1.5rem;
			z-index: 10;
			@include max-lg {
				transform: translate(-50%, 85%);
			}
			@include max-md {
				transform: translate(-50%, calc(100% + 2rem));
			}
			.countdown {
				padding: 0.75rem 3rem;
				border-radius: 10px;
				background-color: var(--color-primary);
				@include max-md {
					padding: 0.75rem 1rem;
				}
			}
			.btn-reminder {
				z-index: 3;
				position: relative;
				margin: 0;
				display: flex;
				align-items: center;
				position: relative;
				gap: 0.5rem;
				transition: all 0.2s ease-in-out;
				path {
					transition: all 0.2s ease-in-out;
				}
			}
		}
	}
}

.envelope-wrap {
	position: fixed;
	z-index: 1300;
	transition: all 1s ease-out;
	background-color: var(--color-main);
	overflow: hidden;
	color: #fff;
	.header-content {
		.header {
			justify-content: space-between;
			.header-title {
				margin-bottom: 0;
				padding: 3rem;
				@include max-md {
					padding: 3rem 0.75rem;
				}
			}
		}
	}
}
