.couple-section {
	position: relative;
	padding: 10rem 0 5rem;
	text-align: center;
	@include max-md {
		padding: 15rem 0 3rem;
		.row {
			gap: 2rem;
		}
	}
	.rose-bg {
		position: absolute;
		bottom: 0;
		left: 50%;
		transform: translate(-50%, 30%);
		height: 110%;
	}
	.and {
		// @include min-md {
		// 	position: absolute;
		// 	top: 40%;
		// 	left: 50%;
		// 	transform: translate(-50%, -40%);
		// }
		margin: 1.5rem 0;
		font-weight: 600;
		color: var(--color-dark);
		text-align: center;
	}
	.couple {
		position: relative;
		display: flex;
		justify-content: center;
		align-items: center;
		@include max-md {
			flex-direction: column;
			&.flex-reverse-sm {
				flex-direction: column-reverse;
			}
		}
		.couple-description {
			position: relative;
			background-color: var(--color-primary);
			border-radius: 10px;
			overflow: hidden;
			@include max-md {
				padding: 160px 0.75rem 1.5rem !important;
				margin: -100px 0 0 !important;
				animation-name: heightDown !important;
			}
			&.man {
				padding: 0.75rem 2rem 0.75rem 160px;
				margin-left: -120px;
			}
			&.woman {
				padding: 0.75rem 160px 0.75rem 2rem;
				margin-right: -120px;
			}
			.orn-1,
			.orn-2 {
				position: absolute;
				left: 0;
				top: 0;
				height: 100%;
			}
			.orn-2 {
				transform: translate(-40%, 40%);
				@include max-md {
					height: 120px;
					transform: translate(-40%, 220%);
				}
			}
			.orn-1 {
				right: 0;
				left: unset;
				transform: translate(35%, 40%) scaleX(-1);
				@include max-md {
					height: 125px;
					transform: translate(35%, 220%) scaleX(-1);
				}
			}
		}
		.image-wrap {
			width: 240px;
			aspect-ratio: 1;
			position: relative;
			z-index: 3;
			@include max-md {
				width: 80%;
			}
			.ornament-couple {
				position: absolute;
				top: 50%;
				left: 50%;
				transform: translate(-50%, -50%);
				width: 135%;
				height: 148%;
				&.reverse {
					transform: translate(-50%, -50%) scaleX(-1);
				}
				img {
					width: 100%;
					height: 100%;
				}
			}
			.couple-image {
				height: 100%;
				object-fit: cover;
				width: 100%;
				border-radius: 1000px;
			}
		}
		.couple-parent {
			width: 100%;
		}
		.sosmed-wrap {
			position: relative;
			width: 100%;
			.sosmed {
				color: var(--color-dark);
				text-decoration: none;
				small {
					align-items: center;
					gap: 0.25rem;
				}
			}
			.fab {
				font-size: 18px;
				@include max-md {
					font-size: 16px;
				}
			}
		}
	}
}
